@import "@/styles/global.scss";































html,
body,
#app {
  height: 100%;
}
#app {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: green;
  .left,
  .right,
  .center {
    height: 100%;
    background-color: rgb(223, 162, 31);
    border: 1px yellow solid;
  }
  .left {
    width: 20%;
    order: 0;
    flex-basis: 60px;
  }
  .center {
    width: 300px;
    flex-grow: 1;
    order: 1;
  }
  .right {
    width: 30%;
    order: 2;
    flex-basis: 60px;
  }
}
