@import "@/styles/global.scss";









































































































































































.pageContrainer {
  height: 100%;
  background-color: #f7f7f7;
}

.bscroll-container {
  // height: 100%;
}
#drop-down {
  position: absolute;
  top: -3rem;
  left: 0px;
  width: 100%;
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-size: 0.8rem;
  color: #ccc;
  z-index: 20000;
}
#tabTitleWrap {
  width: 100;
  height: 2rem;
  display: flex;
  padding: 0rem;
  list-style: none;
  line-height: 2rem;
  background-color: #fff;
  justify-content: space-around;
  z-index: 3;
}
#tabListWrap {
  background-color: #f7f7f7;
  .item {
    width: 95%;
    height: 4rem;
    margin: 0.5rem auto;
    border: 0.1rem solid #fff;
    border-radius: 0.5rem;
    list-style: none;
    margin-top: 0.5rem;
    background: #fff;
    // background: green;
    // padding-top: 2rem;
    line-height: 4rem;
  }
}
#drop-up {
  position: relative;
  top: 2rem;
}
