@import "@/styles/global.scss";








































































#pageContainer {
  height: 100%;
  display: flex;
  flex-direction: column;
  #PageTitle {
    margin-bottom: 300px;
  }
  #pageBody {
    display: flex;
    flex-direction: row;

    .effectModule {
      width: 300px;
      height: 300px;
      color: #fff;
      font-size: 30px;
      border: 1px #ccc solid;
      border-radius: 20px;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      .info {
        color: #000;
        font-size: 20px;
      }
    }
    #debounce {
      background-color: green;
    }
    #throttle {
      background-color: red;
    }
  }
}
