@import "@/styles/global.scss";
















































#top-nav-wrap {
  width: 100%;
  height: 3rem;
  background-color: #22a2c3;
  color: #fff;
  display: flex;
  align-items: center;
  text-align: center;
  p {
    height: 100%;
    display: flex;
    align-items: center;
  }
  #backArrowLeft {
    padding-left: 1rem;
    position: absolute;
  }
  #navBarTitle {
    max-width: 60%;
    margin: 0 auto;
    color: #323233;
    font-weight: 500;
    font-size: 16px;
  }
  #backArrowRight {
    padding-right: 1rem;
    visibility: hidden;
    position: absolute;
  }
  // .this.arrowTitleRight {
  //   visibility: show;
  // }
}
